.d_flex{
  display: flex !important;
}
.flex_column{
  flex-direction: column !important;
}
.justify_content_between{
  justify-content: space-between;
}
.h_100{
  height: 100% !important;
}
.bg_white{
  background-color: #fff;
}
.tab_tools_ico{
   img{
     width: 48px;
     height: 48px;
   }
}
.justify_content_end {
  justify-content: flex-end !important;
}
.p_0{
  padding: 0 !important;
}
.mb{
  &_0{
    margin-bottom: 0 !important;
  }
  &_5{
    margin-bottom: 3rem !important;
  }
}
.ml{
  &_3{
    margin-left: 1rem !important;
  }
}
.text_uppercase{
  text-transform: uppercase !important;
}
.text{
  &_14{
    font-size: 0.875rem;
  }
  &_18{
    font-size: 1.125rem;
  }
  &_grey3{
    color: #6f6e7d !important;
  }
  &_grey4{
    color: #413f53 !important;
  }
  &_primary{
    color: #4579f5 !important;
  }
  &_success{
    color: #16b8b8 !important;
  }
  &_danger{
    color: #f54562 !important;
  }
}

.p_4 {
  padding: 1.5rem !important;
}
.border {
  border: 1px solid #dee2e6 !important;
}
.border_border1 {
  border-color: #f1f1f2 !important;
}
.currency_head_info{
  padding: 24px;
  .currency_head_stats{
    margin-top: 16px;
    margin-bottom: 4px;
  }
  .currency_head_stats_usd_change{
    font-family: Gilroy;
    font-size: 16px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    text-align: right;
  }
}
.text_right{
  text-align: right !important;
}
